const formatPrice = (price) => {
  return new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  }).format(price);
};

export const validationRules = {
  required: (value) => !!value || "This field is required",
  email: (value) => /.+@.+\..+/.test(value) || "Enter a valid email address",
  phone: (value) => /^\d{10,15}$/.test(value) || "Enter a valid phone number (10-15 digits)",
  maxLength: (max) => (value) => (value && value.length <= max) || `Message must be less than ${max} characters`,
};
