<template>
  <v-container>
    <v-card>
      <v-card-title>Summary</v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="isValid">
          <v-text-field
            :label="$t('form.name')"
            v-model="formData.name"
            :rules="[rules.required]"
          />

          <v-text-field
            :label="$t('form.email')"
            v-model="formData.email"
            :rules="[rules.required, rules.email]"
          />

          <v-text-field
            :label="$t('form.address')"
            v-model="formData.address"
            :rules="[rules.required]"
          />

          <v-text-field
            :label="$t('form.phone')"
            v-model="formData.phone"
            :rules="[rules.required, rules.phone]"
          />

          <v-textarea
            :label="$t('form.message')"
            v-model="formData.message"
            auto-grow
            :rules="[rules.maxLength(500)]"
          />

          <v-btn :disabled="isSubmitDisabled" @click="handleSubmit" color="primary">
            {{ $t('common.send') }}
          </v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>

  <auth-dialog
    context="order"
    @authComplete="handleAuthComplete"
    v-model="isLoginDialogOpen"
    @closed="isLoginDialogOpen = false"
  />
</template>

<script>
import { computed, ref, watch } from "vue";
import { useRouter } from "vue-router";
import { useOrdersStore } from "@/stores/ordersStore";
import { validationRules as rules } from "@/utils";
import { useUserStore } from "@/stores/userStore";

import AuthDialog from "@/components/auth/AuthDialog.vue";

export default {
  name: 'OrderForm',
  components: {
    AuthDialog
  },
  setup() {
    const isValid = ref(false);

    const form = ref(null);

    const isLoginDialogOpen = ref(false);

    const formData = ref({
      name: null,
      email: null,
      address: null,
      phone: null,
      message: null,
    });

    const router = useRouter();

    const userStore = useUserStore();

    const profile = computed(() => userStore.profile);

    const basketItems = computed(() => useOrdersStore().basket);

    const isSubmitDisabled = computed(() => {
      return basketItems.value.length === 0 || !isValid.value;
    });

    const handleSubmit = () => {
      if (!isValid.value) return;

      if (!profile.value) {
        isLoginDialogOpen.value = true;
        return;
      }

      useOrdersStore().submitOrder({
        description: formData.value.message,
        items: basketItems.value,
      }).then((orderId) => {
        openOrder(orderId);
      })
    };

    const handleAuthComplete = () => {
      isLoginDialogOpen.value = false;
      console.log('profile.value', profile.value);
      if (!profile.value?.details) {
        useUserStore().updateProfile(formData.value);
      }
    };

    const openOrder = (orderId) => {
      if (!orderId) return;
      router.push('/orders/' + orderId);
    };

    watch(profile, (newVal) => {
      if (newVal) {
        formData.value.name = newVal.name;
        formData.value.email = newVal.email || newVal.details?.email;
        formData.value.phone = newVal.details?.phoneNumber;
      }

      setTimeout(() => {
        if (form.value) {
          form.value.validate();
        }
      });
    }, { immediate: true });

    return {
      rules,
      isLoginDialogOpen,
      isValid,
      formData,
      form,
      isSubmitDisabled,
      handleAuthComplete,
      handleSubmit,
    };
  }
}
</script>